@import "https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&display=swap";
.card {
  box-shadow: none;
  backface-visibility: visible;
  transform-style: preserve-3d;
  width: 20vw;
  height: 20vw;
  letter-spacing: 1px;
  background: none;
  border: none;
  margin: auto auto 2.5%;
  padding: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  transition: all .2s ease-out;
}

.card .inner-card {
  color: #fff;
  will-change: transform, filter;
  float: none;
  height: auto;
  box-sizing: border-box;
  height: 100%;
  filter: drop-shadow(0 15px 15px #0000004d);
  perspective-origin: 0 0;
  letter-spacing: 0;
  background: #000 -3px -3px / calc(100% + 6px);
  border-radius: 5px;
  margin: 0 auto;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: 500;
  line-height: 3rem;
  transition: all .15s ease-out;
  display: block;
  top: 0;
  overflow: hidden;
  transform: rotateX(0)rotateY(0)scale(1);
}

.card.yo .inner-card {
  background-image: url("box.41a236cb.jpg");
}

.card .glare {
  content: "";
  width: 100%;
  opacity: .6;
  pointer-events: none;
  height: 100%;
  z-index: 9999;
  mix-blend-mode: hard-light;
  opacity: 0;
  background: radial-gradient(circle, #c7c6f3, #0000);
  border-radius: 14px;
  transition: all .1s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.card.animated .glare {
  opacity: .3;
}

.elements {
  height: 90%;
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

.info {
  width: 50%;
  text-align: center;
  place-self: flex-end;
  margin: 0 auto 10%;
}

.info h1 {
  color: #f5f5f5;
  text-align: center;
  padding: 1vw 2vw;
  font-family: Josefin Sans, sans-serif;
  font-weight: 600;
  text-decoration: none;
}

.info h2 {
  color: #f5f5f5;
  text-align: center;
  margin-bottom: 1vw;
  padding: 0 2vw;
  font-family: Josefin Sans, sans-serif;
  font-weight: 600;
  text-decoration: none;
}

.info a {
  color: #f5f5f5;
  text-align: center;
  padding: 0 1vw;
  font-family: Josefin Sans, sans-serif;
  font-weight: 600;
}

